import React, { useState, useEffect } from 'react'
import { Redirect,useHistory} from 'react-router-dom'
import Footer from '../Footer'


import Login from '../Login'
import Navbar from '../Navbar'
const Register = () => {

    let history = useHistory()
    const [name,setFname] = useState("")
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const [cpassword,setCpassword] = useState("")
    const [userType,setUserType] = useState("")
    const [phone,setPhone] = useState("")
    const [users,setUsers]  = useState([])
    const [login,setLoginform] = useState("")
    const [lEmail,setLEmail] = useState("")
    const [loggedIN,setLoggedin] = useState(false)
    const [lPassword,setLPassword] = useState("")
    useEffect(() =>{
        const getTasks = async () => {
          const usersFromServer = await fetchUsers()
          setUsers(usersFromServer)
      
        }
        // console.log(users)
        getTasks()
      },[])

      const fetchUsers = async () =>{
          try{
  const res = await fetch('https://hidden-forest-99115.herokuapp.com/api/users/')
  
          const data = await res.json()
          return data
          }catch(error){
              console.log(error)
          }
      }
    const onSubmit = (e) =>{
      
        // setUsers(fname,email,password,userType,phone)
        if(userType){
        addUser({name,email,password,userType,phone});
        alert('User successfully created. You can now login')
        }else{alert("please select a user")}
        
    }
    const signin =   (e) => {
e.preventDefault()
        
        const email = lEmail
        const password = lPassword
loggin({email,password})

setLEmail("")
        setLPassword("")
    }


const loggin = async (user) => {
    try{
    const res = await fetch('https://hidden-forest-99115.herokuapp.com/api/users/login',{
            method:'POST',
            headers: {
                'Content-type':'application/json',
                'Accept': 'application/json'
            },
            body:JSON.stringify(user)

        })
       const data =await res.json()
        history.push("/profile",{
            token:data
        }) ;
       
    }catch(error){
        console.warn("Login",error)
    }


 
}
    
  
    
    const addUser = async (user) =>{
       console.log(user)
       try{
        const res = await fetch('https://hidden-forest-99115.herokuapp.com/api/users/register', {
            method: 'POST',
            headers: {
                'Content-type':'application/json'
            },
            body:JSON.stringify(user)
        })

        const data = await res.json()
        setUsers([...users,data])
    }catch(error){
        console.log(error)
    }
        const createdUser = {...user} 
        console.log(createdUser)

        // setUsers([...users,createdUser])


    }
//     function usee(){
//    // setUserType('data')
//     return true
//     }
    return (
       
        <div className=''>
            <Navbar></Navbar>
            {loggedIN?<Redirect to={{ pathname: '/Profile' , state: { email: {email} } } } />: ""}
            <h1 className='sign-up m-2 '>Hello There</h1>
            
            <div className='form-reg m-10' >
            <div className='container s-container upper-bar'>

            <h3 className='headwe'>Create Your account</h3>
<button className={`btn--norad  ${userType===1?'btn--outline p-2':'btn--primary'}`} onClick={(e) => setUserType(1)} > Agent </button>
<button className={`btn--norad  ${userType===2?'btn--outline p-2':'btn--primary'}`} onClick={(e) => setUserType(2)}> Developer </button>
<button className={`btn--norad  ${userType===3?'btn--outline p-2':'btn--primary'}`} onClick={(e) => setUserType(3)}> Landlord </button>
<button className={`btn--norad  ${userType===4?'btn--outline p-2':'btn--primary'}`} onClick={(e) => setUserType(4)}> User </button>
            
                <form onSubmit={onSubmit}>
                    <div className='row'>
                    <div className='form-group col-sm-6'>
                    <label>Full Name</label>
                    <input className='form-control' value={name} required minLength='6' onChange={(e) => setFname(e.target.value)} />
                    </div>
                    </div>
                    <div className='row'>
                    <div className='form-group col-sm-6'>
                    <label>Email Address</label>
                    <input className='form-control' type="email" required autoComplete='email' value={email} onChange={(e) => setEmail(e.target.value)}/>
                    </div>
                    </div>
                    <div className='row'>
                    <div className='form-group col-sm-6'>
                    <label>Password</label>
                    <input className='form-control' required type="password" autoComplete='password' value={password}  onChange={(e) => setPassword(e.target.value)}/>
                    </div>
                    </div>
                    <div className='row'>
                    <div className='form-group col-sm-6'>
                    <label>Confirm password</label>
                    <input className='form-control' type="password" required autoComplete='password' value={cpassword} onChange={(e) => setCpassword(e.target.value)}/>
                    </div>
                    </div>
                    <div className='row'>
                    <div className='form-group col-sm-6'>
                    <label>Phone Number</label>
                    <input className='form-control' type="phone" required autoComplete='phone' value={phone} onChange={(e) => setPhone(e.target.value)}/>
                    </div>
                    
                    
                    </div>
                    <div className='row'>
                    <div className='form-group '>
                        <input className='btn--norad col-lg-5 btn--primary ' type='submit' value='register'  />
                    </div>
                    </div>
                    
                    


                </form>
                </div>
                </div>
                <div className='s-container form-reg'>
                <button className='btn btn--medium btn--outline' onClick={(e) => setLoginform(!login)}  >Sign In  </button>
                {login?(
                    <div className='container s-container'>
                    <form onSubmit={signin}>
                        <div className='row'>
                        <label>Email Address</label>
                        <input value={lEmail} type="email" onChange={(e) => setLEmail(e.target.value)} placeholder='Email'  /> 
                        </div>
                        <div className='row'>
                        <label>password</label>
                        <input value={lPassword} type="password" onChange={(e) => setLPassword(e.target.value)} placeholder='Enter Password'  /> 
                        </div>
                        <div className='row'>
                        <button >Log in</button>
                        </div>
                        
                         
                    </form>
                    </div>


                ):<Login />}
                
                
            </div>
           
<Footer/>
        </div>
    )
}

export default Register
