import React, {useState, useEffect} from 'react'

import { Link } from 'react-router-dom'
import { Button } from './Button'
import Login from './Login'
import  logo from './Logo.svg'
import './Navbar.css'


function Navbar(props) {
    const [click,setClick] = useState(false)
    const loggedIn = useState(props.isloggedin)
    const [button,setButton] = useState(true)
    const handleClick = () =>setClick(!click)
    const closeMobileMenu = () => setClick(false)
    const showButton = () =>{
        if(window.innerWidth <= 960){
            setButton(false)
        }else{
            setButton(true)
        }
    }

    useEffect(() => {
        showButton();
      }, []);
    window.addEventListener('resize',showButton)
    return (
        <> 
        <nav className="navbar">
            <div className="navbar-container">
                <Link to="/" className="navbar-logo">
                    <img src={logo} alt="NPL" width='100px' height='60px'/>
                    
                </Link>
                <div className='menu-icon' onClick={handleClick}>
                     <i className={click ? 'fas fa-times' :'fas fa-bars'} />
                </div>
                <ul className={click? 'nav-menu active' : 'nav-menu'}>
                    <li className='nav-item'>
                        <Link to='/' className='nav-links' onClick={closeMobileMenu}>Home</Link>
                    </li>
                    <li className='nav-item'>
                        <Link to='/Sale' className='nav-links' onClick={closeMobileMenu}>For Sale</Link>
                    </li>
                    <li className='nav-item'>
                        <Link to='/Rent' className='nav-links' onClick={closeMobileMenu}>For Rent</Link>
                    </li>
                    <li className='nav-item'>
                        <Link to='/Requests' className='nav-links' onClick={closeMobileMenu}>Requests</Link>
                    </li>
                    <li className='nav-item'>
                        <Link to='/Register' className='nav-links' onClick={closeMobileMenu}>Register</Link>
                    </li>
                </ul>
                {button&& !loggedIn && <Button buttonStyle='btn--primary' >Register</Button>}
                {button && !loggedIn  && <Login/>}
                {button && loggedIn && <Link to={{pathname:'./Profile', state:{ id: props.id}  }}><img src='' alt='' width='50px' height='50px' style={{'borderRadius':'50%'}}  /></Link>}
            </div>
        </nav>
        </>
    )
}

export default Navbar
