import React, { useState} from 'react'
import '../Button.css'
import Footer from '../Footer'
import '../Forms.css'
import Navbar from '../Navbar'

const Requests = () => {
        const [location,setLocation] = useState("")
        const [Bedrooms,setBedroom] = useState("")
        const [budget, setBudget] = useState("")
        const [requests,setRequest] = useState([])
        const [type,setType] = useState("")
        const [fname,setFname] = useState("")
        const [email,setEmail] = useState("")
        const [phone,setPhone] = useState("")
        const [serviced,isServiced] = useState(true)
        const [furnished,isFurnished] = useState(true)
        const [comment,setComment] = useState("")
    //     useEffect(() => {
    //         const getRequests = async () =>{
    //             const requestsFromServer = await fetchRequests()
    // setRequest(requestsFromServer)
    //         }
    //         getRequests()
            
    //     }, [])
        const onSubmit = (e)=> {
            e.preventDefault()
            const request = {location:location,bed:Bedrooms,budget:budget,type:type,serviced:serviced,furnished:furnished,name:fname,email:email,comment:comment,}
        
            addRequest(request)
            alert("Request successfully made")
            setLocation("")
            setBedroom("")
            setBudget("")
            setType("")
            setComment("")
            
        }



        const addRequest = async (request) =>{
            
            const res = await fetch('http://localhost:4000/requests',{
            method: 'POST',
            headers: {
            'Content-type':'application/json',

            },
            body : JSON.stringify(request)
        })
      const data = await res.json()
      setRequest([...requests, data])

     
      const id = Math.floor(Math.random() * 1000) + 1
      const newRequest = {id, ...request}
      console.log(newRequest);
      setRequest([...requests,newRequest])

        }

    return (
        <div>
            <Navbar isloggedin={false}></Navbar>
            <h1 className='requests'>Requests</h1>
            <div className='container s-container p-3 col-sm-push-10'>
            <form onSubmit={onSubmit} >
            <div className='row p-3'>
                <div className='form-group  m-2 col-lg-12 col-sm-12'>  
                <label>Location</label> 
            <input className='form-control  ' type='text' value={location} onChange={(e) => setLocation(e.target.value)}  placeholder='Enter A State, Locality or Area' />
                </div>
                </div>
                <div className='row'>
                <div className=' col-xs-3 col-lg-2  col-sm-2'>  
                Rooms
               <select className='form-control' value={Bedrooms} onChange={(e) => setBedroom(e.target.value)}>
                        <option selected disabled >Select Number of Rooms </option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                        <option>6</option>
                        <option>7</option>
                        <option>8</option>
                    </select>
                    </div>
                    <div className=' col-xs-4 col-lg-2 col-sm-2 '>
                      <label>  Lease Type</label>
                    <select className='form-control '  value={type} onChange={(e) => setType(e.target.value)} >
                        <option>Rent</option>
                        <option>Sale</option>
                        <option>Lease</option>
                        <option>Joint Venture</option>
                    </select>  
                    </div>
                    <div className=' col-lg-2 col-xs-5 col-sm-2 '>
                        <label>Your Budget</label>
                    <input className='form-control' type='text' value={budget} onChange={(e) => setBudget(e.target.value)}  placeholder='Enter your budget in naira' />
                </div>
                </div>
                <div>
                <div className='input-group col-lg-2 col-xs-2 col-sm-2'>
                <label>Serviced</label>
                &nbsp;
                <input className='form-control checkbox-inline' type="checkbox" value={serviced} 
                onChange={(e) => isServiced(e.currentTarget.checked)}/>
                </div>
                <div className='input-group  col-lg-3 col-sm-3 col-sm-3'>
                <label >Furnished</label>
                <input className='form-control checkbox-inline ' type="checkbox" value={furnished} 
                onChange={(e) => isFurnished(e.currentTarget.checked)}/>
                </div>
                </div>
                <div className='row m-2'>
                <div className='form-group  m-1  col-lg-3 col-sm-3 col-sm-3'>
                    <label>Full Name</label>
                    <input className='form-control  ' type='text' value={fname} onChange={(e) => setFname(e.target.value)}  placeholder='Enter your name here' />
            </div>
                <div className='form-group  m-1 col-lg-3 col-sm-3 col-sm-3'>
                    <label>Email</label>
                    <input className='form-control  ' type='email' value={email} onChange={(e) => setEmail(e.target.value)}  placeholder='Enter your Email' />
       
                </div>
                <div className='form-group  m-1 col-lg-2 col-sm-2 col-sm-2'>
                    <label>Phone</label>
                    <input className='input form-control  col-bg-6' type='phone' value={phone} onChange={(e) => setPhone(e.target.value)}  placeholder='Enter your phone number' />
                       </div>
                </div>
                <div className='row'>
                <div className=" mb-2 col-sm-12">
              <label for="comments">Comments</label>
                <textarea className="textarea form-control" rows="10" name="comments" value={comment} onChange={(e) => setComment(e.target.value)} required ></textarea>
                
              </div>
                <div className='form-group mt-2'>
                <input className='d-flex btn btn--primary btn--medium m-2' type='submit' name='Submit'  value='Submit Request'/>
                </div>
                </div>
            </form>
            </div>
            <Footer></Footer>
        </div>
    )
}

export default Requests
