import GoogleLogin from 'react-google-login'
import { useHistory} from 'react-router-dom'

export default function Login() {
  let history = useHistory()
    
  const clientID = '403949553367-u5mto5tmnqttm68j4n37a4hkr1ssukh7.apps.googleusercontent.com';
  const onFailure = (res) => {
console.table(res)
  }
  const onSuccess= async (res) => {
     // const img_url = res.profileObj['imageUrl'];
      const names  = res.profileObj['name']
     
      const email = res.profileObj['email'];
      // TODO:
      // check if user exists
      // If user exist goto profile else passowrd setup and register as user
      const data  = await fetch('https://hidden-forest-99115.herokuapp.com/api/users/verify',{
      method: 'POST',
      headers:{
        'Content-type':'application/json'
      },
      body:JSON.stringify({email})
    })
    const exists = await data.json()
    if(exists){
      console.log(exists)
     history.push("/Profile", {token : exists})
    }else{
        history.push("/passwordsetup",{name:names,email:email})}
      

  }
  return (
        <div>
            <GoogleLogin 
                clientId={clientID}
                buttonText="Login with Google"
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedin={true}
                
                />
        </div>
    )
}
