import React, {useState} from 'react'
import {useHistory,useLocation} from 'react-router-dom'
const PasswordSetup = () => {
    let location  = useLocation()
    let history = useHistory()
    const name = location.state.name
    const email = location.state.email
    const [password,setPassword] = useState("")
    const [cPass,setCPass] = useState("")
    const [phone,setPhone] = useState("")
    const [userType,setUserType] = useState("")
    const onSubmit = (e) => {
        e.preventDefault();
       
        // add new user from google
        let userInfo = {email,name,password,phone,userType}
        //console.table(userInfo)
        const newuser  = addUser(userInfo);
        if( newuser === "user created"){
        //redirect to profile
         history.push("/Profile",{
            email:email,
            userType:userType
        })
        }else{
            console.table("error",newuser)
        }
    }

   const  addUser = async (user) =>{
       try{
    const res = await fetch('https://hidden-forest-99115.herokuapp.com/api/users/register', {
        method: 'POST',
        headers: {
            'Content-type':'application/json'
        },
        body:JSON.stringify(user)
    })
    const data = await res.json()
    console.log(data)
}catch(error){
    console.log("Add user error",user)
    console.log(error)
}

    


    }

    // function redirect(path,details){
    //     history.push({pathname:path,state:{
    //         details
    //     }})

    // }
    return (
        <div>
            <div className='container s-container m-2'>
                <form onSubmit={onSubmit}>
                    <h2>Finish Setting up your account {name} : '{email}'</h2>
                    <div className='row mb-2 col-lg-6'>
                        <label> Enter Password</label>
                        <div className='input-group'>
                            <span className='input-group-addon'><i className='fas fa-user-lock'></i></span>
                            <input className=' m-2 form-control' type='password' placeholder='Please Enter a password ' value={password} onChange={(e) => setPassword(e.target.value)}></input>
                        </div>
                    </div>
                    <div className='row mb-2 col-lg-6'>
                        <label> Confirm Password</label>
                        <div className='input-group'>
                            <span className='input-group-addon'><i className='fas fa-user-lock'></i></span>
                            <input className='m-4 form-control' type='password' placeholder='confirm your password ' value={cPass} onChange={(e) => setCPass(e.target.value)}></input>
                        </div>
                    </div>
                    <div className='row mt-2 m-2 mb-2 p-4 col-lg-6'>
                        <label> UserType</label>
                        <div className='input-group'>
                            <span className='input-group-addon'><i className='fas fa-users'></i></span>
                            <select className='form-control' value={userType} onChange={(e) => {setUserType(e.target.value)}}>
                                <option value='1' >User</option>
                                <option value='2'>Agent</option>
                                <option value='3'>Developer</option>
                                <option value='4'>Landlord</option>
                            </select>
                        </div>
                    </div>
                    <div className='row p-4 col-lg-6'>
                        <label>Phone</label>
                        <div className='input-group'>
                            <span className='input-group-addon'><i className='fas fa-phone'></i></span>
                            <input className='form-control' type='tel' pattern="[0-9]{3}[0-9]{4}[0-9]{4}"  placeholder='Please Enter your phone number ' value={phone} onChange={(e) => setPhone(e.target.value)}></input>
                        </div>
                    </div>
                    
                    <div className='row mb-2 col-lg-6' >
                        <label></label>
                    <div className='mb-2 input-group'>
                            <span className="input-group-addon"><i className="fa fa-user-check"></i></span>
                            <input className='mb-2 form-control btn btn--primary' type='submit' value='Complete signup' />
                            </div>
                    </div>
                </form>

            </div>
        </div>
    )
}

export default PasswordSetup
