import './App.css'
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom'
import Home from './components/pages/Home';
import Rent from './components/pages/Rent';
import Sale from './components/pages/Sale';
import Requests from './components/pages/Requests';
import Register from './components/pages/Register';
import Profile from './components/pages/Profile';
import AddProperty from './components/pages/AddProperty';
import Contact from './components/pages/Contact';
import SingleProperty from './components/SingleProperty'
import PasswordSetup from './components/pages/PasswordSetup'
// import Index from './components/pages/Index'
function App() {
  return (
    <>
    <Router>

     {/* <Navbar></Navbar> */}
      <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/Rent" exact component={Rent}/>
        <Route path="/Sale" exact component={Sale}/>
        <Route path="/Requests" exact component={Requests}/>
        <Route path="/Register" exact component={Register}/>
        <Route path="/Profile" exact render={(props) => {<Profile {...props} />}} component={Profile} />
        <Route path="/AddProperty" exact component={AddProperty} />
        <Route path="/Contact" exact component={Contact} />
        <Route path='/PasswordSetup' render={(props) => {<PasswordSetup {...props}/>}} exact component={PasswordSetup} />
        <Route path="/SingleProperty" exact render={(props) => {<SingleProperty {...props} />}} component={SingleProperty}/>
        {/* <Route path='/Admin/Index' exact component={Index}/> */}
      </Switch>
    </Router>
    </>
  );
}

export default App;
