import React, {useEffect,useState} from 'react'
import { useLocation } from 'react-router'
import Footer from '../Footer'
import jwt_decode from "jwt-decode";
import './Profile.css'
import Navbar from '../Navbar';
import PropertyCard from '../PropertyCard';

const Profile = () => {
    let location = useLocation()
const info = jwt_decode(location.state.token)
const id = info._id
const [title,setTitle] = useState("")
const [proplocation,setPropLocation] = useState("")
const [price,setPrice] = useState("")
const [bedrooms,setBedroom] = useState("")
const [bathroom,setBaths] = useState("")
const [toilets,setToilet] = useState("")
const [serviced,setServiced] = useState("")
const [furnished,setFurnished ] = useState("")
const [type,setType] = useState("")
const [comment,setComment ] = useState("")
const [shared,setShared] = useState("")
const [listing,setListings]  = useState([])
const [user,setUser] = useState([])

useEffect(() => {
    const getUser = async (id) => {
         const user = await fetchUser(id)
        setUser(user)
     
        }
    const getUserListings = async (id) => {
        const res = await fetchListings()
 
  setListings(res.filter((listing) =>(listing.agent_id === id)))

    }
        
 getUser(id)
 getUserListings(id)
 

}, [])

const fetchUser = async (id) =>{

    const res = await fetch(`https://hidden-forest-99115.herokuapp.com/api/users/${id}`)
    const data = await res.json()
    return data
}
const fetchListings = async () => {
    const res  = await fetch('https://hidden-forest-99115.herokuapp.com/api/listings')
    const data  = await  res.json()
    return data
}

    // Add Post{
    const AddPost = async  (post) =>{
        try{
        const res  = await fetch('https://hidden-forest-99115.herokuapp.com/api/listings',{
        method: 'POST',
         headers:{
            'Content-type' : 'application/json'
        },
        body:JSON.stringify(post)
    })
    const data = await res.json()
    alert('Post Created',data)
        }catch(error){
            alert('Error adding Post',error)
        }
    

    }
   const onsubmit = (e) => {
       e.preventDefault()
       const location = proplocation
      const  post = {title,type,location,price,agent_id: user._id,bedrooms,bathroom,toilets,serviced,furnished,shared}
    
      AddPost(post)
    }
    
 
    return (
        <div>
            <Navbar isloggedin={true} ></Navbar>
            <div className='profile'>
                <div>
                { user.img_src === 'undefined'?<img className={`img-fluid ${user.img_src?"":'fas fa-person'}`} src={`${user.img_src === undefined?user.img_src:user.img_src}`} alt={user.name} width='200px' height='200px'/>:<h1> <span width='200px'  height='200px' className='fa fa-user-circle'></span></h1>}
                </div> 
                <div className='m-3 p-3'>
                <h1>Name : {user.name}</h1>
                <p>E-mail : {user.email}</p>
                <p>Phone number : {user.phone}</p>
                </div>   
            </div>
            <div className='addPost'  style={{borderBottm:'10px'}}>
                <div className='container s-container col-lg-12 addPost mb-2 '>
                <h3>my posts: <span className='fa fa-plus-circle' >Add Post</span> </h3>
                
                    <form onSubmit={onsubmit}>
                        <div className='row ' style={{marginBottom:'5px'}}>
                            <div className='col-lg-9'>
                        <label>Title</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-info"></i></span>
                        <input className='form-control ' type='text' required value={title} onChange={(e) => setTitle(e.target.value)} />
                        </div>
                        </div>
                        </div>
                        <div className='row' style={{marginBottom:'5px'}}>
                            <div className='col-lg-3'>
                            <label>Price</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-coins"></i></span>
                        <input className='form-control' type='text' required value={price} onChange={(e) => setPrice(e.target.value)} />
                        </div>
                            </div>
                            <div className='col-lg-3'>
                        <label>Type:</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-toilet"></i></span>
                        <select className='form-control' value={type} required onChange={(e) => setType(e.target.value)}>
                            <option>Rent</option >
                            <option>Lease</option >
                            <option>Sell</option >
                            <option>Joint Venture</option >
                            <option>Short Let</option>
                        </select>
                        </div>
                        </div>
                            <div className='col-lg-3'>
                        <label>Location</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-map-marker"></i></span>
                        <input className='form-control' type='text' required value={proplocation} onChange={(e) => setPropLocation(e.target.value)} />
                        </div>
                        </div>
                        </div>
                        <div className='row' style={{marginBottom:'5px'}}>
                        <div className='col-lg-3'>
                        <label>Bedrooms:</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-bed"></i></span>
                        <select className='form-control' value={bedrooms} required onChange={(e) => setBedroom(e.target.value)}>
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                            <option>5</option>
                            <option>6</option>
                            <option>6+</option>
                        </select>
                        </div>
                        </div>
                        <div className='col-lg-3'>
                        <label>Bathrooms:</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-shower"></i></span>
                        <select className='form-control' value={bathroom} required onChange={(e) => setBaths(e.target.value)}>
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                            <option>5</option>
                            <option>6</option>
                            <option>6+</option>
                        </select>
                        </div>
                        
                        </div>
                        <div className='col-lg-3'>
                        <label>Toilets:</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-toilet"></i></span>
                        <select className='form-control' value={toilets} required onChange={(e) => setToilet(e.target.value)}>
                            <option>1</option >
                            <option>2</option >
                            <option>3</option >
                            <option>4</option >
                            <option>5</option >
                            <option>6</option >
                            <option>6+</option >
                        </select>
                        </div>
                        </div>
                        </div>
                        <div className='row' style={{marginBottom:'5px'}}>
                        
                        <div className='col-lg-3'>
                        <label>Furnished</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-chair"></i></span>
                        <select className='form-control' value={furnished} required onChange={(e) => setFurnished(e.target.value)}>
                            <option>Yes</option>
                            <option>No</option>
                            <option>Doesn't Apply</option>
                        </select>
                        </div>
                        </div>
                        <div className='col-lg-3'>
                        <label>Serviced</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-tools"></i></span>
                        <select className='form-control' value={serviced} required onChange={(e) => setServiced(e.target.value)}>
                            <option>Yes</option>
                            <option>No</option>
                            <option>Doesn't Apply</option>
                        </select>
                        </div>
                        </div>
                        <div className='col-lg-3'>
                        <label>Shared</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-users"></i></span>
                        <select className='form-control' value={shared} required onChange={(e) => setShared(e.target.value)}>
                            <option>Yes</option>
                            <option>No</option>
                            <option>Doesn't Apply</option>
                            
                        </select>
                        </div>
                        </div>
                        </div>
                        <div className='row 'style={{marginBottom:'5px'}}>
                        <div className='col-lg-9'>
                            <label>Image</label>
                        <div className='input-group'>
                        <span className="input-group-addon" ><i className="fa fa-file-upload" ></i></span>
                        
                        
                        </div>
                        </div>
                        </div>
                        <div className='row'>
                        <div className='col-lg-9'>
                            <label>Additonal information</label>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-comments"></i></span>
                            <textarea className='form-control' rows='12' required value={comment} onChange={ (e) => setComment(e.target.value)}/>
                            </div>
                            </div>
                            </div>
                            <div className='row'>
                            <div className='col-lg-6 ' style={{margin:'10px'}}>
                        <div className='input-group'>
                        <span className="input-group-addon"><i className="fa fa-plus"></i></span>
                            <input className='btn--norad btn--outline' type='submit' value='Submit' text='Upload Property'/>
                            </div>
                            </div>
                        </div>
                    </form>

                </div>
                <div className="" style={{marginTop:'20px'}}>
                {listing.length >0 ?<PropertyCard property={listing}></PropertyCard>:""
                }
            </div>
            </div>
            
           

            <Footer></Footer>
        </div>
        
    )
}

export default Profile
